/* Call to Action Button */
.cta-btn {
  display: inline-block;
  position: relative;
  padding: 0.8rem 1.6rem;
  font-weight: bold;
  line-height: 1;
  z-index: 1;
  -webkit-transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.6s;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.6s; }
  .cta-btn::after {
    content: '';
    display: block;
    position: absolute;
    width: 0px;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
    -webkit-transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.3s;
    transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.3s; }

/* Hero Style */
.cta-btn--hero {
  background-image: -webkit-linear-gradient(315deg, #04660c 0%, #499c11 100%);
  background-image: linear-gradient(135deg, #04660c 0%, #499c11 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 2px solid transparent;
  -webkit-border-image: -webkit-linear-gradient(315deg, #04660c 0%, #499c11 100%);
          border-image: linear-gradient(135deg, #04660c 0%, #499c11 100%);
  border-image-slice: 1; }
  @media (max-width: 37.5em) {
    .cta-btn--hero {
      background-image: none;
      border: 2px solid #499c11;
      -webkit-text-fill-color: #499c11; } }
  .cta-btn--hero::after {
    background-image: -webkit-linear-gradient(315deg, #04660c 0%, #499c11 100%);
    background-image: linear-gradient(135deg, #04660c 0%, #499c11 100%); }
    @media (max-width: 37.5em) {
      .cta-btn--hero::after {
        background-image: none; } }
  .cta-btn--hero:hover {
    -webkit-text-fill-color: #fff;
    text-decoration: none; }
    @media (max-width: 37.5em) {
      .cta-btn--hero:hover {
        -webkit-text-fill-color: #499c11; } }
    .cta-btn--hero:hover::after {
      width: 100%; }

/* Resume Style */
.cta-btn--resume {
  color: #fff;
  border: 2px solid #fff; }
  .cta-btn--resume::after {
    background: #fff; }
  .cta-btn--resume:hover {
    color: #499c11;
    text-decoration: none; }
    .cta-btn--resume:hover::after {
      width: 100%; }

/* Arrow Button */
.up i {
  color: #272727; }

.footer {
  background-color: #333333;
  color: #fff;
  padding: 4.8rem 0; }
  @media (max-width: 37.5em) {
    .footer {
      border: 0px; } }
  .footer__text {
    color: gray;
    font-size: 1.3rem; }
    .footer__text a {
