    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in; }
    .social-links a:hover {
      -webkit-transform: translateY(-2px);
              transform: translateY(-2px); }

.back-to-top i {
  color: #fff;
  margin: 1rem 0 1.6rem;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in; }
  .back-to-top i:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px); }

section {
  padding: 5rem 0rem; }
  @media (max-width: 37.5em) {
    section {
      border: none;
      padding-left: 1rem;
      padding-right: 1rem; } }

/*---HERO SECTION---*/
