/* ---ABOUT SECTION--- */
#about {
  background-color: #04660c;
  background-image: -webkit-linear-gradient(315deg, #04660c 0%, #499c11 100%);
  background-image: linear-gradient(135deg, #04660c 0%, #499c11 100%);
  color: #fff;
  height: 95vh;
  margin-top: 100vh;
  border-top: 0px;
  box-shadow: 0px 19px 38px rgba(0, 0, 0, 0.3), 0px 15px 12px rgba(0, 0, 0, 0.22);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 80vh, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 80vh, 0 100%); }
  @media (max-width: 75em) {
    #about {
      height: 100%;
      -webkit-clip-path: none;
      clip-path: none; } }
  @media (max-width: 37.5em) {
